.alignment {
  text-align: center;
  border-radius: none !important;
  font-family: "Roboto";

  position: relative;
}

.searchMenu {
  position: relative;
  padding-top: 15px;
  padding-bottom: 15px;
}
.searchBar {
  position: relative;
  width: 55%;
  left: 5px;
  line-height: 30px;
  border-radius: 10px;
  border: 1px solid grey;

  padding: 15px;
  padding-left: 40px;

  font-family: Arial, Helvetica, sans-serif;
}

.fas {
  position: relative;
  z-index: 1;

  left: 15px;

  padding: 10px;
  padding-bottom: 25px;
}

.jumbotron {
  text-align: center;
  background-color: #e6e6e6;
}
